"use client";

import { useQueryState } from "nuqs";
import {
  Box,
  Button,
  Flex,
  For,
  HStack,
  Image,
  Separator,
  Stack,
} from "@chakra-ui/react";
import { MidText } from "@/components/ui/typography/midText";
import { SubText } from "@/components/ui/typography/subText";
import { responsive } from "@/utils/constants";
import img from "@/assets/imgs/customisations.png";

export const Preview = () => {
  const [device, setDevice] = useQueryState("device");

  return (
    <Box
      w="100%"
      minH="100%"
      bg="white"
      border="1px solid"
      borderRadius={"8px"}
      borderColor={"#E5E7EB"}
    >
      <Flex justify={"space-between"} p={"16px 25px"}>
        <Stack gap={"0px"}>
          <MidText text="template_preview" />
          <SubText fs="14px" text="template_desc" />
        </Stack>

        <HStack>
          <For each={responsive}>
            {({ id, name, icon }) => (
              <Button
                key={id}
                variant={device === name ? "subtle" : "ghost"}
                onClick={() => setDevice(name)}
              >
                {icon}
              </Button>
            )}
          </For>
        </HStack>
      </Flex>

      {/* <Separator /> */}

      <Image
        w="100%"
        h="685px"
        objectFit={"contain"}
        src={img.src}
        p={"60px 25px 0 25px"}
      />
    </Box>
  );
};
